<template>
  <div id="app">
    <slot name="header"></slot>

    <div id="main">
      <slot name="bar"></slot>
      <div class="a-content" :style="{'top': headerHeightVW+'vw', 'right': -(0/3.75)+'vw'}">
          <a-scroll ref="scroller"
            :pullDownRefresh="pullDownRefreshObj"
            @pullingDown="onPullingDown"
            :pullUpLoad="pullUpLoadObj"
            @pullingUp="onPullingUp"
            :scrollbar="scrollbarObj"
            @scroll="onScroll"
            @scroll-end="onScrollEnd"
            listenScroll
            listenScrollEnd
          >

              <slot></slot>
          </a-scroll>
          <slot name="blank"></slot>
      </div>
    </div>

    <page-view v-if="Object.keys(this).indexOf('$route')>=0"></page-view>
  </div>
</template>

<script>
import AScroll from './Scroll'
import PageView from './View'

export default {
  components: {
    AScroll,
    PageView
  },
  props: {
    pullDownRefresh: {
      type: Boolean,
      default: false,
    },
    scrollbar: {
      type: Boolean,
      default: false,
    },
    pullUpLoad: {
      type: Boolean,
      default: false,
    },
    headerHeightVW: {
      type: Number,
      default: 66/3.75,
    },
  },
  data() {
    return {
      pullDownRefreshThreshold: 90,
      pullDownRefreshStop: 40,
      scrollbarFade: true,
      pullUpLoadThreshold: 0,
      canLoad: true,
      messsgeInterval: null,
      endLoading: false
    }
  },
  computed: {
    pullDownRefreshObj() {
      return this.pullDownRefresh ? {
        threshold: parseInt(this.pullDownRefreshThreshold),
        stop: parseInt(this.pullDownRefreshStop),
      } : false
    },
    scrollbarObj() {
      return this.scrollbar ? { fade: this.scrollbarFade } : false
    },
    pullUpLoadObj() {
      return this.pullUpLoad ? {
        threshold: parseInt(this.pullUpLoadThreshold),
        txt: { more: this.pullUpLoadMoreTxt, noMore: this.pullUpLoadNoMoreTxt },
      } : false
    },
  },
  created () {
  },
  mounted () {

    if (window.chrome) {
      this.$emit('on-ready', {"l":0,"t":0,"w":375,"h":46})
    }

    window.apiready = () => {
      // const header = $api.byId('header')
      var header = document.querySelector('#header')

      if (header) {
        $api.fixStatusBar(header)
        console.log('xxxxxxxxxxxxxx')
        const headerPos = $api.offset(header)
        const main = $api.byId('main')
        const mainPos = $api.offset(main)

        this.$emit('on-ready', headerPos, mainPos)
      } else {
        this.$emit('on-ready')
      }

      api.addEventListener({
        name:'viewappear'
      }, (ret, err) =>{
        console.log('Window 显示到屏幕的事件')
        this.$emit('on-show')
      })

      // 监听键盘弹出
      api.addEventListener({
        name:'keyboardshow'
      },(ret, err) => {
        console.log('...........', ret.h)
      })

      // 监听键盘返回被点击
      api.addEventListener({
        name: 'keyback'
      }, (ret, err) => {
        // alert(JSON.stringify(ret))
      })
    }
  },
  methods: {
    onPullingDown() {

      this.$emit('on-refresh')
    },
    finishRefresh() {
      this.$refs.scroller.forceUpdate(true)
      this.$refs.scroller.openPullUp()
      this.canLoad = true
      this.endLoading = false
      this.$refs.scroller.openPullUp()
    },
    scrollRefresh () {
      this.$refs.scroller.refresh()
    },
    onPullingUp() {
      // 更新数据
      if (!this.endLoading && this.canLoad) {
        this.canLoad = false
        
        this.$nextTick(() => {
          this.$emit('on-load')
        })
      } else {
        // this.endLoad()
      }

      if (this.endLoading) {
        this.endLoad()
      }
    },
    updateLoad() {
      this.canLoad = true
      this.$refs.scroller.forceUpdate(true)
    },
    endLoad() {
      this.$refs.scroller.forceUpdate(false)
      this.$nextTick(() => {
        this.$refs.scroller.closePullUp()
        this.canLoad = false
        this.endLoading = true
      })
    },
    openPullUp () {
      this.$refs.scroller.openPullUp()
    },

    scrollTop () {
      this.$refs.scroller.scrollTop()
    },

    scrollTo (x, y, time = 300) {
      this.$refs.scroller.scrollTo(x, y, time)
    },

    initScroll () {
      this.$refs.scroller.scrollTop()
      this.$refs.scroller.openPullUp()
      this.canLoad = true
    },

    onScroll (pos) {
      this.$emit('on-scroll', pos)
    },
    onScrollEnd (pos) {
    },

    getScrollPosition () {
      return this.$refs.scroller.getPosition()
    },
    disable() {
      this.$refs.scroller.disable()
    },
    enable() {
      this.$refs.scroller.enable()
    },

  },
}
</script>


