import axios from 'axios'
import qs from 'qs'
import navigate from '../utils/navigate'

let request

if (window.chrome) {
  request = axios.create({
    withCredentials: true,
  })

  request.interceptors.request.use(
    (config) => {
      config.headers = {
        // 'X-Openerp-Session-Id': window.localStorage.getItem('token'),
        'Content-Type': 'application/x-www-form-urlencoded',
      }

      config.data = qs.stringify(config.data)

      return config
    },
    (error) => {
      console.log('error', error)
      return Promise.reject(new Error(error).message)
    },
  )

  request.interceptors.response.use(
    (response) => {
      console.log('response', response)
      
      if (typeof response.data !== 'object') {
        // return Promise.reject('Internal Server Error')
        navigate.redirectLogin()
      }
      if (response.data.errcode === 0) {
        return response.data.data
      }
      if (response.data.errcode === 2) {
        navigate.open('reset_password')
        return response.data.data
      }

      const lang = window.localStorage.getItem('language')
      return Promise.reject(lang == 'en-US' ? response.data.msg2 : response.data.msg)
    },
    (error) => {
      
      console.log('error', error)
      return Promise.reject(new Error(error).message)
    },
  )
} else {
  request = {
    get(url, params) {
      return this.send(url, 'get', params.params)
    },
    post(url, params, files) {
      return this.send(url, 'post', params, files)
    },
    send(url, method, params, files) {
      console.log('---------#########----------')
      console.log(url)
      console.log(JSON.stringify(params))
      return new Promise((resolve, reject) => {
        api.ajax({
          url,
          method,
          data: {
            values: params,
            files,
          },
          headers: {
            // 'X-Openerp-Session-Id': window.localStorage.getItem('token'),
          },
          timeout: 600
        }, (ret, err) => {
          console.log('===========#####===========')
          console.log(url)
          console.log(JSON.stringify(ret))
          const lang = window.localStorage.getItem('language')
          if (ret && typeof ret === 'object') {
            if (ret.errcode === 0) {
              resolve(ret.data)
            }
            if (ret.errcode === 2) {
              navigate.open('reset_password')

              resolve(ret.data)
            }
            reject(lang == 'en-US' ? ret.msg2 : ret.msg)
          } else {
            console.log(JSON.stringify(err))
            if (err.code == 3) {
              navigate.redirectLogin()
            } else if (lang == 'en-US') {
              reject(err & err.msg2 ? err.msg2 : 'Internal Server Error')
            } else {
              reject(err & err.msg ? err.msg : '网络请求超时，请稍后重试')
            }
          }
        })
      })
    },
  }
}

export default request
