  saveAddress (list) {
    return new Promise((resolve, reject) => {
      idb.openDB('hsdb').then((db) => {
        const trans = db.transaction(['address'], 'readwrite')
        const addressStore = trans.objectStore('address')

        const oldAddress = []

        addressStore.openCursor().onsuccess = (e) => {
          const cursor = e.target.result
          if (cursor) {
            oldAddress.push(cursor.value)
            cursor.continue()
          } else{
            
            // 查找删除的地址
            for (let i = 0; i < oldAddress.length; i++) {
              let index = list.findIndex(item => item.id == oldAddress[i].id)

              if (index < 0) {
                addressStore.delete(oldAddress[i].id)
              }
            }

            // 更新或者新增地址
            for (let i = 0; i < list.length; i++) {
              let index = oldAddress.findIndex(item => item.id == list[i].id)

              if (index >= 0) {
                addressStore.put({...list[i]})
              } else {
                addressStore.add({...list[i]})
              }
            }
          }
        }

        trans.oncomplete = (e) => {
          resolve()
        }

      })
    })
  },
