  getAddress (parentId) {
    return new Promise((resolve, reject) => {
      idb.openDB('hsdb').then((db) => {
        
        const trans = db.transaction(['address'], 'readwrite')
        const addressStore = trans.objectStore('address')

        const address = []

        addressStore.openCursor().onsuccess = (e) => {
          const cursor = e.target.result
          if (cursor) {
            address.push(cursor.value)
            cursor.continue()
          }
        }

        trans.oncomplete = (e) => {
          let filterAddress = []
        
          filterAddress = address.filter(item => item.parentId == parentId)

          resolve(filterAddress)
        }
      })
    })
  },
