  getMyFault () {
    return new Promise((resolve, reject) => {
      idb.openDB('hsdb').then((db) => {
        const trans = db.transaction(['myfault'], 'readwrite')
        const myfaultStore = trans.objectStore('myfault')

        const fault = []
        myfaultStore.openCursor().onsuccess = (e) => {
          const cursor = e.target.result
          if (cursor) {
            fault.push(cursor.value)
            cursor.continue()
          }
        }

        trans.oncomplete = (e) => {

          resolve(fault.map(item => ({
            id: item.id,
            no: '-',
            type: 1,
            state: 'create',
            level: item.level && item.level.name,
            content: item.content,
            line: item.line && item.line.name,
            station: item.station && item.station.name,
            createTime: item.time,
            timeConsuming: 0,
          })))
        }
      })
    })
  },
