import io from '../api'

export const uploadImage = (imageList) => {
  const imageArray = []
  return new Promise((resolve, reject) => {
    if (imageList.length) {
      let imageFilter = api.require('imageFilter')
      for (let i = 0; i < imageList.length; i++) {

        if (imageList[i].url && imageList[i].url.substr(0, 7) == 'http://') {
          imageArray.push({
            ...imageList[i]
          })

          if (imageArray.length == imageList.length) {
            console.log('image complete')
            resolve(imageArray)
          }
        } else {
          imageFilter.getAttr({
            path: imageList[i].url
          }, (ret1, err1) => {
            const tempFolder = 'fs://imgTemp/'
            const tempFileName = new Date().getTime() + '.jpg'
  
            imageFilter.compress({
              img: imageList[i].url,
              quality: 1,
              size: {
                w: 600,
                h: 600 * ret1.height / ret1.width
              },
              save: {
                imgPath: tempFolder,
                imgName: tempFileName
              }
            }, (ret2, err2) => {
              let imgFile = tempFolder + tempFileName
              
              io.uploadImg({file: imgFile}).then(d => {
                imageArray.push({
                  ...imageList[i],
                  ...d
                })
      
                if (imageArray.length == imageList.length) {
                  console.log('image complete')
                  resolve(imageArray)
                }
              }).catch(msg => {
                reject(msg)
              })
            })
          })
        }

      }
    } else {
      resolve([])
    }
  })
}

