export default {
  openDB(name, version = 6) {
    const request = window.indexedDB.open(name, version)

    return new Promise((resolve, reject) => {
      request.onerror = (e) => {
        reject(e.currentTarget.error.message)
      }
      request.onsuccess = (e) => {
        resolve(e.target.result)
      }
      request.onupgradeneeded = (e) => {
        const db = e.target.result
        if (!db.objectStoreNames.contains('order')) {
          const store = db.createObjectStore('order', { keyPath: 'id' })
        }

        if (!db.objectStoreNames.contains('problem')) {
          const store = db.createObjectStore('problem', { keyPath: 'id' })
        }

        if (!db.objectStoreNames.contains('address')) {
          const store = db.createObjectStore('address', { keyPath: 'id' })
        }

        if (!db.objectStoreNames.contains('deviecategory')) {
          const store = db.createObjectStore('deviecategory', { keyPath: 'id' })
        }

        if (!db.objectStoreNames.contains('myfault')) {
          const store = db.createObjectStore('myfault', {keyPath: 'id'})
        }

        if (!db.objectStoreNames.contains('myproblem')) {
          const store = db.createObjectStore('myproblem', {keyPath: 'id'})
        }
      }
    })
  },
}
