import { getQueryStringByName } from './tools'

export default {
  open(name, params, animation, back = false, softInputMode = 'auto') {
    try {
      window.api.openWin({
        name,
        url: `${name}.html`,
        pageParam: params,
        animation: animation || {},
        slidBackEnabled: back,
        slidBackType: 'edge',
        softInputMode,
      })
    } catch {
      if (params) {
        if (typeof params === 'object') {
          params = JSON.stringify(params)
        }
        const ecParams = encodeURI(params)
        window.location.href = `/${name}.html?params=${ecParams}`
      } else {
        window.location.href = `/${name}.html`
      }
    }
  },
  copen(name, child, params, animation) {
    try {
      window.api.openWin({
        name: `${name  }_${  child}`,
        url: `${name}.html?child=${child}`,
        pageParam: params,
        animation: animation || {},
        slidBackEnabled: false,
        slidBackType: 'edge',
        reload: true,
      })
    } catch {
      if (params) {
        if (typeof params === 'object') {
          params = JSON.stringify(params)
        }
        const ecParams = encodeURI(params)
        window.location.href = `/${name}.html?params=${ecParams}`
      } else {
        window.location.href = `/${name}.html`
      }
    }
  },

  redirectLogin() {

    try {
      const ajpush = window.api.require('ajpush')
      ajpush.bindAliasAndTags({
        alias: localStorage.getItem('deviceToken'),
        tags: ['0'],
      })

      window.localStorage.removeItem('token')
      window.localStorage.removeItem('user')

      window.api.openWin({
        name: 'login',
        url: 'login.html',
        animation: {
          type: 'movein',
          subType: 'from_bottom',
          duration: 300,
        },
        slidBackEnabled: false,
        slidBackType: 'edge',
      })
    } catch {
      window.location.href = 'login.html'
    }
  },

  backLogin() {
    try {
      window.api.closeWin({
        animation: {
          type: 'movein',
          subType: 'from_top',
        },
      })
    } catch {
      window.history.back()
    }
  },

  getParams() {
    try {
      return window.api.pageParam
    } catch {
      const paramsTxt = decodeURI(getQueryStringByName('params'))
      try {
        return JSON.parse(paramsTxt)
      } catch {
        return paramsTxt
      }
    }
  },

  getParam(name) {
    try {
      const params = window.api.pageParam
      if (params[name]) {
        return params[name]
      }
      return null
    } catch {
      const paramsTxt = decodeURI(getQueryStringByName('params'))
      try {
        const params = JSON.parse(paramsTxt)
        return params[name]
      } catch {
        return null
      }
    }
  },

  back(name) {
    try {
      if (name) {
        window.api.closeWin({
          name,
        })
      } else {
        window.api.closeWin()
      }
    } catch {
      if (!name) {
        window.history.back()
      }
    }
  },

  backTo(name) {
    try {
      window.api.closeToWin({
        name,
      })
    } catch {
      window.location.href = `/${name}.html`
    }
  },
}
