import Vue from 'vue'
import Vant, { Locale } from 'vant';
import FastClick from 'fastclick';
import 'vant/lib/index.css'
import '../styles/common.scss'
import zhCN from 'vant/lib/locale/lang/zh-CN';
import enUS from 'vant/lib/locale/lang/en-US';
import Cookies from 'js-cookie'
import i18n from '../language'
import config from '../config'
import { loadJs, getQueryStringByName } from "./tools"
import io from '../api'

export default {
  init: (app, router, store) => {
    const language = localStorage.getItem('language')

    if (language) {
      if (language === 'zh-CN') {
        Locale.use('zhCN', zhCN)
      } else {
        Locale.use('enUS', enUS)
      }
    } else {
      Locale.use('zhCN', zhCN);
    }

    Vue.config.productionTip = false;

    FastClick.prototype.focus = (targetElement) => {
      targetElement.focus()
    }

    FastClick.attach(document.body)

    Vue.use(Vant)

    // 处理不同的登录平台
    if (config.platform === 'qywx') {
      loadJs('http://res.wx.qq.com/open/js/jweixin-1.2.0.js').then(() => {
        io.sign({
          platform: 'qywx',
          serial: config.serial,
          url: window.location.href.replace(window.location.hash, ''),
        }).then((data) => {
          window.wx.config({
            beta: true,
            debug: config.debug,
            appId: data.corpId,
            timestamp: data.timeStamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: config.wxApiList,
          })

          window.wx.ready(() => {
            if (getQueryStringByName('code')) {
              io.login({
                platform: 'qywx',
                serial: config.serial,
                code: getQueryStringByName('code'),
              }).then((data) => {
                Cookies.set('token', data.token)

                new Vue({
                  i18n,
                  router,
                  store,
                  render: h => h(app),
                }).$mount('#app');
              })
            } else {
              window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${data.corpId}&redirect_uri=${window.location.href.replace(window.location.hash, '')}&response_type=code&scope=snsapi_base&state=${config.state}#wechat_redirect`
            }
          })

          window.wx.error((err) => {
            window.alert(err.errMsg)
          })
        }).catch((msg) => {
          window.alert(msg)
        })
      })
    } else if (config.platform === 'dd') {
      loadJs('//g.alicdn.com/dingding/dingtalk-jsapi/2.6.0/dingtalk.open.js').then(() => {
        io.sign({
          platform: 'dd',
          url: window.location.href.replace(window.location.hash, ''),
        }).then((data) => {
          window.dd.config({
            agentId: data.agentId,
            corpId: data.corpId,
            timeStamp: data.timeStamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: config.ddApiList,
          })

          window.dd.ready(() => {
            window.dd.runtime.permission.requestAuthCode({
              corpId: data.corpId,
              onSuccess: (info) => {
                console.log('++++', info)
                io.login({ code: info.code, account_code: config.serial, platform: 'dd' }).then((data) => {
                  Cookies.set('token', data.token)

                  new Vue({
                    i18n,
                    router,
                    store,
                    render: h => h(app),
                  }).$mount('#app');
                })
              },
            })
          })

          window.dd.error((err) => {
            alert(err.errorMessage)
          })
        }).catch((msg) => {
          window.alert(msg)
        })
      })
    } else {
      new Vue({
        i18n,
        router,
        store,
        render: h => h(app),
      }).$mount('#app');
    }
  },
}
